<template>
  <div class="enter">
    <RainbowBubbles :config="config" :bubbles="bubbles" />

    <div class="show">
      <div class="show-name" @click="open">z7xisoo</div>
      <div class="show-more" @click="$router.push('/zlogs')">Go</div>
      <div class="show-icp">粤ICP备20041949号</div>
    </div>

    <div class="block-item">
      <div class="item" @click="handleBackground(0)"></div>
      <div class="item" @click="handleBackground(1)"></div>
      <div class="item" @click="handleBackground(2)"></div>
    </div>

    <div class="block-npm">
      <div class="item" @click="handleNpm(0)">Waves</div>
      <div class="item" @click="handleNpm(1)">Bubbles</div>
    </div>
  </div>
</template>

<script>
import RainbowBubbles from "rainbow-bubbles";
import image from "img/chuan.png";
export default {
  name: "Enter",
  components: {
    RainbowBubbles,
  },
  computed: {},
  watch: {},
  data() {
    return {
      config: {},
      bubbles: {
        max: {
          number: 5,
          rate: 0.5,
          radius: [15, 20],
          blowHeight: 0,
          background: {
            type: "color",
            color: [
              "blue",
              "red",
              "orange",
              "green",
              "#EE6363",
              "#FFA07A",
              "LightGreen",
              "BlueViolet",
              "#00BFFF",
            ],
          },
        },
      },
    };
  },
  mounted() {
    this.config = {
      new: true,
      background: {
        type: "gradient",
        color: ["red", "orange", "green", "blue", "DarkCyan"],
        position: [0, 0, window.innerWidth, window.innerHeight],
      },
    };
  },
  methods: {
    open() {
      window.open("https://github.com/z7xisoo", "_blank");
    },
    handleNpm(index) {
      switch (index) {
        case 0:
          window.open("https://www.npmjs.com/package/rainbow-waves", "_blank");
          break;
        default:
          window.open("https://www.npmjs.com/package/rainbow-bubbles", "_blank");
          break;
      }
    },
    handleBackground(index) {
      switch (index) {
        case 0:
          this.config.background = {
            type: "gradient",
            color: ["red", "orange", "green", "blue", "DarkCyan"],
            position: [0, 0, window.innerWidth, window.innerHeight],
          };
          break;
        case 1:
          this.config.background = {
            type: "color",
            color: "DarkCyan",
          };
          break;
        case 2:
          this.config.background = {
            type: "image",
            src: image,
            repetition: "repeat",
          };
          break;
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.enter {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;

  .block-item {
    position: fixed;
    display: flex;
    left: 0;
    bottom: 0;
    .item {
      width: 0.5rem;
      height: 0.5rem;
      background-color: DarkCyan;
      margin: 0.1rem;
      border-radius: 0.04rem;
      cursor: pointer;
      border: 1px solid #FFDAB9;
      &:first-child {
        background: linear-gradient(135deg, red, orange, green, blue, DarkCyan);
      }
      &:last-child {
        background-image: url("~img/chuan.png");
        background-size: cover;
      }
    }
  }

  .block-npm {
    position: fixed;
    display: flex;
    right: 0;
    bottom: 0;
    .item {
      width: .7rem;
      height: 0.5rem;
      margin: 0.1rem;
      border-radius: 0.04rem;
      cursor: pointer;
      color: #fff;
      text-align: center;
      line-height: .5rem;
      font-family: z7xisoo-crude;
      background-color: #CD0000;
    }
  }
  .show {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    .show-name {
      font-size: 2.4rem;
      font-weight: bolder;
      margin-bottom: 0.3rem;
      color: #fff;
      font-family: z7xisoo-crude;
      border-bottom: 6px solid springgreen;
      cursor: pointer;
      padding: 0 0.4rem;
      transition: all 0.5s;
      &:hover {
        color: #ff3030;
        border-bottom: 6px solid #fff;
      }
    }

    .show-more {
      font-size: 0.24rem;
      cursor: pointer;
      padding: 0 0.4rem;
      color: #fff;
      border: 2px solid springgreen;
      border-radius: 0.04rem;
      text-align: center;
      font-family: z7xisoo-crude;
      font-weight: bolder;
      margin-bottom: 0.2rem;
      background-color: orange;
      display: flex;
      align-items: center;
      &:hover {
        background-color: #fff;
        color: #ff3030;
      }
    }

    .show-icp {
      font-family: z7xisoo-fine;
      letter-spacing: .08rem;
      color: #fff;
    }
  }
}
</style>